(function () {

///////////////////////////////////////////////////////////////////////
//                                                                   //
// packages/jagi:reactive-map/lib/reactive_map.js                    //
//                                                                   //
///////////////////////////////////////////////////////////////////////
                                                                     //
var stringify = function(value) {                                    // 1
  if (value === undefined) {                                         // 2
    return 'undefined';                                              // 3
  }                                                                  // 4
  return EJSON.stringify(value);                                     // 5
};                                                                   // 6
                                                                     // 7
var parse = function(serialized) {                                   // 8
  if (serialized === undefined || serialized === 'undefined') {      // 9
    return undefined;                                                // 10
  }                                                                  // 11
  return EJSON.parse(serialized);                                    // 12
};                                                                   // 13
                                                                     // 14
var changed = function(v) {                                          // 15
  v && v.changed();                                                  // 16
};                                                                   // 17
                                                                     // 18
var ensureKey = function(key) {                                      // 19
  if (!_.has(this._keyDeps, key)) {                                  // 20
    this._keyDeps[key] = new Tracker.Dependency;                     // 21
    this._hasDeps[key] = new Tracker.Dependency;                     // 22
  }                                                                  // 23
};                                                                   // 24
                                                                     // 25
var setObject = function(object) {                                   // 26
  var self = this;                                                   // 27
                                                                     // 28
  _.each(object, function(value, key) {                              // 29
    self.set(key, value);                                            // 30
  });                                                                // 31
};                                                                   // 32
                                                                     // 33
ReactiveMap = function(dictName) {                                   // 34
  this._size = 0;                                                    // 35
  this._values = {};                                                 // 36
  this._sizeDeps = new Tracker.Dependency;                           // 37
  this._allDeps = new Tracker.Dependency;                            // 38
  this._keysDeps = new Tracker.Dependency;                           // 39
  this._valuesDeps = new Tracker.Dependency;                         // 40
  this._keyDeps = {};                                                // 41
  this._hasDeps = {};                                                // 42
};                                                                   // 43
                                                                     // 44
_.extend(ReactiveMap.prototype, {                                    // 45
  set: function(key, value) {                                        // 46
    if (_.isObject(key) && value === undefined) {                    // 47
      setObject.call(this, key);                                     // 48
      return;                                                        // 49
    }                                                                // 50
                                                                     // 51
    serializedValue = stringify(value);                              // 52
                                                                     // 53
    if (_.has(this._values, key)) {                                  // 54
      if (serializedValue === this._values[key]) {                   // 55
        return;                                                      // 56
      }                                                              // 57
      this._valuesDeps.changed();                                    // 58
    } else {                                                         // 59
      this._size++;                                                  // 60
      this._sizeDeps.changed();                                      // 61
      changed(this._hasDeps[key]);                                   // 62
      this._keysDeps.changed();                                      // 63
      this._valuesDeps.changed();                                    // 64
    }                                                                // 65
                                                                     // 66
    this._values[key] = serializedValue;                             // 67
                                                                     // 68
    this._allDeps.changed();                                         // 69
    changed(this._keyDeps[key]);                                     // 70
  },                                                                 // 71
                                                                     // 72
  get: function(key) {                                               // 73
    ensureKey.call(this, key);                                       // 74
    this._keyDeps[key].depend();                                     // 75
                                                                     // 76
    return parse(this._values[key]);                                 // 77
  },                                                                 // 78
                                                                     // 79
  has: function(key) {                                               // 80
    ensureKey.call(this, key);                                       // 81
    this._hasDeps[key].depend();                                     // 82
                                                                     // 83
    return _.has(this._values, key);                                 // 84
  },                                                                 // 85
                                                                     // 86
  delete: function(key) {                                            // 87
    if (!_.has(this._values, key)) {                                 // 88
      return;                                                        // 89
    }                                                                // 90
                                                                     // 91
    delete this._values[key];                                        // 92
    this._size--;                                                    // 93
                                                                     // 94
    changed(this._keyDeps[key]);                                     // 95
    changed(this._hasDeps[key]);                                     // 96
    this._sizeDeps.changed();                                        // 97
    this._allDeps.changed();                                         // 98
    this._keysDeps.changed();                                        // 99
    this._valuesDeps.changed();                                      // 100
  },                                                                 // 101
                                                                     // 102
  entries: function() {                                              // 103
    this._allDeps.depend();                                          // 104
                                                                     // 105
    var allValues = {};                                              // 106
                                                                     // 107
    _.each(this._values, function(value, key) {                      // 108
      allValues[key] = parse(value);                                 // 109
    });                                                              // 110
                                                                     // 111
    return allValues;                                                // 112
  },                                                                 // 113
                                                                     // 114
  keys: function() {                                                 // 115
    this._keysDeps.depend();                                         // 116
                                                                     // 117
    var keys = [];                                                   // 118
                                                                     // 119
    _.each(this._values, function(value, key) {                      // 120
      keys.push(key);                                                // 121
    });                                                              // 122
                                                                     // 123
    return keys;                                                     // 124
  },                                                                 // 125
                                                                     // 126
  values: function() {                                               // 127
    this._valuesDeps.depend();                                       // 128
                                                                     // 129
    var values = [];                                                 // 130
                                                                     // 131
    _.each(this._values, function(value, key) {                      // 132
      values.push(parse(value));                                     // 133
    });                                                              // 134
                                                                     // 135
    return values;                                                   // 136
  },                                                                 // 137
                                                                     // 138
  size: function() {                                                 // 139
    this._sizeDeps.depend();                                         // 140
                                                                     // 141
    return this._size;                                               // 142
  },                                                                 // 143
                                                                     // 144
  clear: function() {                                                // 145
    var self = this;                                                 // 146
                                                                     // 147
    var oldValues = this._values;                                    // 148
    this._values = {};                                               // 149
    this._size = 0;                                                  // 150
                                                                     // 151
    this._sizeDeps.changed();                                        // 152
    this._allDeps.changed();                                         // 153
    this._keysDeps.changed();                                        // 154
    this._valuesDeps.changed();                                      // 155
                                                                     // 156
    _.each(oldValues, function(value, key) {                         // 157
      changed(self._keyDeps[key]);                                   // 158
      changed(self._hasDeps[key]);                                   // 159
    });                                                              // 160
  }                                                                  // 161
});                                                                  // 162
                                                                     // 163
// Define aliases.                                                   // 164
ReactiveMap.prototype.all = ReactiveMap.prototype.entries;           // 165
                                                                     // 166
///////////////////////////////////////////////////////////////////////

}).call(this);
